

import Vue from 'vue'
import App from './App'

import router from './router/index'
import store from './store/index'
import "./assets/css/reset.css"
import './assets/js/rem-common'



import utils from "./tools/utils"
import ajax from './api/api';
import $ajax from "./api/request"


import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'


Vue.use(ToastPlugin, { position: 'middle' })
Vue.use(LoadingPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)


import Vant from 'vant';
import 'vant/lib/index.css';
Vue.use(Vant);


import vLoading from "./components/vLoading/vLoading";
import vToast from "./components/vToast/vToast";

Vue.use(vLoading);
Vue.use(vToast);




Vue.config.productionTip = false
Vue.prototype.$utils = utils;
Vue.prototype.$api = ajax
Vue.prototype.$ajax = $ajax


/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')



router.beforeEach((to, from, next) => {
if (to.path == "/first" && from.path == "/") {
next('/first/firstTwo')
}
if (to.matched.length === 0) { //匹配前往的路由不存在
next(from.name ? { name: from.name } : '/error') 
} else {
next(); 
}
})


